.app__navbar{
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    position: fixed;
    z-index: 2;
}

.app__navbar-logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-logo img{
    width: 90px;
    height: 20px;
}

.app__navbar-logo svg{
    width: 20rem;
    height: 45px;
}

.app__navbar-logo svg text.text-body{
    font-size: 3.4rem;
    font-weight: 700;
    letter-spacing: -5px;
    fill: rgba(72,138,204,1);
    /* fill: #8141e1; */
    /* stroke: blue; */
    stroke-width: 2;
    /* animation: 10s infinite alternate animate-stroke; */
}
.app__navbar-logo svg:hover text.text-body{
    transition: all 1s ease-in-out;
    animation: 10s infinite alternate animate-stroke;

}

@keyframes animate-stroke{
    10%{
        fill: #8141e1;
        stroke: rgba(54,95,160,1);
        stroke-width: 3;
        stroke-dashoffset: 25%;
        stroke-dasharray: 0 32%;
    }
    50%{
        fill: rgba(72,138,20,0);
        stroke: rgba(54,95,160,1); 
        stroke-width: 3;
        letter-spacing: -3px;
    }
    90%, 100%{
        fill: #8b3ed2;
        stroke: #8b3ed2; 
        stroke-width: 0;
        stroke-dashoffset: -25%;
        stroke-dasharray: 32% 0;
    }
}

.app__navbar-links{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;
}

.app__navbar-links li{
    margin: 0 1rem;
    cursor: pointer;

    flex-direction: column;
}

.app__navbar-links li:hover div{
    color: var(--secondary-color);
}

.app__navbar-links li div{
    width: 5px;
    height: 5px;
    background-color: transparent;
    border-radius: 50%;

    margin-bottom: 5px;
}

.app__navbar-links li a{
    color: var(--gray-color);
    text-decoration: none;
    flex-direction: column;

    text-transform: uppercase;
    font-weight: 500;

    transition: all 0.3s ease-in-out;
}
.app__navbar-links li a:hover{
    color: var(--secondary-color);
}


@media screen and (max-width: 2000px) {

    .app__navbar-logo img{
        width: 180px;
        height: 40px;
    }
    
}


@media screen and (max-width: 900px) {

    .app__navbar-logo svg{
        width: 16rem;
        height: 45px;
    }

    .app__navbar-links{
        display: none;
    }
    
    .app__navbar-menu{
        width: 35px;
        height: 35px;
        border-radius: 50%;
        position: relative;

        display: flex;
        justify-content: center;
        align-items: center;
        background: var(--hover-gradient);
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.55);
    }

    .app__navbar-menu svg{
        width: 70%;
        height: 70%;
        color: var(--white-color);
    }

    .app__navbar-menu div{
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;

        width: 80%;
        height: 100vh;
        padding: 1rem;

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;

        background: url(../../assets/bgWhite.png);
        background-color: var(--white-color);
        background-size: cover;
        background-repeat: repeat;

        box-shadow: 0 0 20px rgba(168, 168, 168, 0.15);
    }

    .app__navbar-menu div svg{
        width: 35px;
        height: 35px;
        color: var(--secondary-color); 
        margin: 0.5rem 1rem;
    }

    .app__navbar-menu div ul{
        list-style: none;
        margin: 0;
        padding: 0;
        height: 100%;
        width: 100%;

        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
    }

    .app__navbar-menu div ul li{
        margin: 1rem;
    }

    .app__navbar-menu div ul li a{
        color: var(--gray-color);
        text-decoration: none;
        font-size: 1rem;
        font-weight: 500;
        text-transform: uppercase;
        transition: all 0.3s ease-in-out;
    }

    .app__navbar-links li a:hover{
        color: var(--secondary-color);
    }
   
}

@media screen and (min-width: 900px){
    
    .app__navbar-menu{
        display: none;
    }

}