.app__footer{
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__footer-cards{
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap-reverse;
    margin: 4rem 2rem 2rem;
}

.app__footer-card{
    min-width: 290px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    margin: 1rem 0;
    padding: 1rem;
    border-radius: 10px;
    cursor: pointer;
    background-color: #fef4f5;

    transition: all 0.3s ease-in-out;
}

.app__footer-card img{
    width: 40px;height: 40px;
    margin: 0 0.7rem;
}

.app__footer-card p{
    font-weight: 600;
}

.app__footer-card a{
    text-decoration: none;
    font-weight: 500;
}

.app__footer-card:hover{
    box-shadow: 0 0 25px #fdeff0;
}

.app__footer-cards .app__footer-card:last-child{
    background-color: #f2f7fb;
}

.app__footer-form{
    width: 60%;
    flex-direction: column;
    margin: 1rem 2rem;
}

.app__footer-form div{
    width: 100%;

    margin: 0.75rem 0;
    border-radius: 10px;
    cursor: pointer;
    background-color: var(--primary-color);

    transition: all 0.3s ease-in-out;
}

.app__footer-form div input,
.app__footer-form div textarea{
    width: 100%;
    padding: 0.95rem;
    border: none;
    border-radius: 7px;
    background-color: var(--primary-color);

    font-family: var(--font-base);
    color: var(--secondary-color);
    outline: none;
}

.app__footer-form div textarea{
    height: 170px;
}

.app__footer-form div:hover{
    box-shadow: 0 0 25px var(--primary-color);
}

.app__footer-form button{
    margin: 2rem 0 0 0;
    padding: 1rem 2rem;
    font-family: var(--font-base);
    border-radius: 10px;
    border: none;
    background: var(--background-gradient);

    font-weight: 500;
    color: var(--white-color);
    outline: none;
    cursor: pointer;
}



@media screen and (max-width: 768px) {
    .app__footer-cards{
        width: 100%;
    }

    .app__footer-form{
        width: 100%;
        margin: 1rem 0;
    }
}

@media screen and (max-width: 450px) {
    .app__footer-card{
        width: 100%;
    }
}