.app__skill {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__skill-container{
    width: 80%;
    margin-top: 3rem;

    display: flex;
    flex-direction: row;

}

.app__skill-list{
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;

    margin-right: 5rem;
}

.app__skill-item{
    flex-direction: column;
    text-align: center;

    margin: 1rem;
    transition: 0.3 ease-in-out;
}

.app__skill-item div{
    width: 90px;height: 90px;
    border-radius: 50%;
    background-color: #fff;
}

.app__skill-item div img{
    width: 50%;height: 50%;
}

.app__skill-item div:hover{
    box-shadow: 0 0 25px #fef4f5;
}

.app__skill-item p{
    font-weight: 500;
    margin-top: 0.5rem;
}
/* ------------ End skill black ------------ */

/* ------------ Start experience black ------------ */
.app__skill-exp{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
}

.app__skill-exp-item{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 1rem 0;
}

.app__skill-exp-works{
    flex: 1;
}

.app__skill-exp-work{
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 1rem;
    cursor: pointer;

    position: relative;
}

.app__skill-exp-work h4{
    font-weight: 500;
}

.app__skill-exp-work p{
    font-weight: 400;
    color: var(--gray-color);
    margin-top: 5px;
}

.app__skill-exp-year{
    margin-right: 3rem;
}

.app__skill-exp-year p{
    font-weight: 800;
    color: var(--secondary-color);
}

.app__skill-exp-work .skill-tooltip{
    display: none;
    position: absolute;
    bottom: 50px;
    right: -20%;

    width: max-content;
    max-width: 400px !important;
    background-color: var(--white-color) !important;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.1) !important;
    border-radius: 5px !important;
    padding: 1rem !important;
    color: var(--gray-color) !important;
    text-align: center !important;
    line-height: 1.5 !important;
    opacity: 1 !important;
}

.app__skill-exp-work .skill-tooltip::after{
    position: absolute;
    content: '';
    border-top: 10px solid var(--white-color);
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    bottom: -10px;
    right: 90%;
    transform: translateX(50%);
}

.app__skill-exp-work:hover .skill-tooltip{
    display: inline-block;
}

.app__skill-exp-work:hover .skill-tooltip p{
    margin: 0;
}





@media screen and (min-width: 2000px) {
    .app__skill-item div{
        width: 150px;height: 150px;
    }

    .app__skill-item{
        margin: 1rem 2rem;
    }

    .app__skill-item p{
        margin: 1rem;
    }

    .app__skill-exp-work{
        width: 80%;
    }

    .app__skill-exp-work .skill-tooltip{
        top: -100%;

        font-size: 1.75rem !important;
        max-width: 500px !important;
        line-height: 2 !important;
    }

}

@media screen and (max-width: 1500px) {
