#home{
    position: relative;
    background: url('../../assets/bgIMG.png');
    background-size: cover;
    background-repeat: repeat;
    background-position: center;
}

#home .app__wrapper{
    padding: 0;
}

#home .app__wrapper .copyright{
    display: none;
}

.app__header{
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;

    padding: 6rem 2rem 0;
}

.app__header-info{
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
}

.app__header-badge{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
}

.app__header-badge .badge-cmp,
.app__header-badge .tag-cmp{
    padding: 1rem 2rem;
    border: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.app__header-badge .badge-cmp .head-text{
    background: var(--hover-gradient) text;
    -webkit-text-fill-color: transparent;
  }

.app__header-badge .tag-cmp{
    flex-direction: column;
    margin-top: 3rem;
}

.tag-cmp p{
    width: 100%;
    text-transform: uppercase;
    text-align: right;
}

.app__header-badge span{
    font-size: 2.5rem;
}



.app__header-circles{
    flex: .75px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;
}

.app__header-circles div{
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-sizing: 0 0 20px rgba(0,0,0,0.1);
}
.app__header-circles div:nth-child(1){
    width: 100px;
    height: 100px;
}
.app__header-circles div:nth-child(2){
    margin: 1.75rem;
    width: 150px;
    height: 150px;
}
.app__header-circles div:nth-child(3){
    width: 70px;
    height: 70px;
}

.app__header-circles div img{
    max-width: 60%;
    height: 60%;
}

.app__header-img{
    flex: 1;
    height: 100%;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;
}

.app__header-img img{
    width: 100%;
    object-fit: contain;
    z-index: 1;
}

.app__header-img .overlay_circle{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
}


@media screen and (min-width: 2000px) {
    .app__header{
        padding-top: 8rem;
    }

    .app__header-badge span{
        font-size: 5rem;
    }

    .app__header-circles div:nth-child(1){
        width: 400px;
        height: 400px;
    }
    .app__header-circles div:nth-child(2){
        width: 170px;
        height: 170px
    }
    .app__header-circles div:nth-child(3){
        width: 200px;
        height: 200px;
    }
}

@media screen and (max-width: 2000px) {
    .app__header-info{
        width: 100%;
        margin-right: 0;
    }
}

@media screen and (max-width: 1200px) {
    .app__header{
        flex-direction: column;
    }

    .app__header-badge{
        justify-content: flex-start;
        align-items: flex-start;
    }

    .app__header-circles{
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;
    }

    .app__header-circles div{
        margin: 1rem;
    }

    .app__header-img{
        margin: 2rem 0;
    }
}

@media screen and (max-width: 450px) {
    .app__header{
        padding: 6rem 1rem 2rem;
    }
}